import Typography from "components/Typography";
import "./styles.scss";
import Button from "components/Button";

type EditProps = {
  children?: React.ReactNode;
  title: string;
  label?: string;
  onClose: () => void;
};

const Edit: React.FC<EditProps> = ({ children, title, label, onClose }) => {
  return (
    <div className="edit">
      <div className="edit__close" onClick={onClose} />
      <div className="edit__inner">
        <Typography variant="h3" className="edit__title">
          {title}
        </Typography>
        {label && (
          <Typography className="edit__label" variant="subtitle1">
            {label}
          </Typography>
        )}

        <div className="edit__content">{children}</div>
        <div className="edit__buttons">
          <Button size="large" onClick={onClose} variant="second">
            Отменить
          </Button>
          <Button size="large" onClick={onClose}>
            Сохранить
          </Button>
        </div>
      </div>
    </div>
  );
};

export default Edit;
