import { Route, Routes } from "react-router-dom";

import { paths } from "constants/paths";

import HomePage from "pages/HomePage";
import UsersPage from "pages/UsersPage";
import UsersIdPage from "pages/UsersPage/UsersIdPage";
import PartnersPage from "pages/PartnersPage";
import PartnersIdPage from "pages/PartnersPage/PartnersPage";
import OrdersPage from "pages/OrdersPage";
import OrdersIdPage from "pages/OrdersPage/OrdersIdPage";
import CatalogPage from "pages/CatalogPage";
import EventsPage from "pages/EventsPage";
import SettingsPage from "pages/SettingsPage";
import SupportPage from "pages/SupportPage";
import EventsIdPage from "pages/EventsPage/EventsIdPage";

const Router = () => {
  return (
    <Routes>
      <Route path={paths.home} element={<HomePage />} />
      <Route path={paths.users} element={<UsersPage />} />
      <Route path={paths.usersId} element={<UsersIdPage />} />
      <Route path={paths.partners} element={<PartnersPage />} />
      <Route path={paths.partnersId} element={<PartnersIdPage />} />
      <Route path={paths.orders} element={<OrdersPage />} />
      <Route path={paths.ordersId} element={<OrdersIdPage />} />
      <Route path={paths.catalog} element={<CatalogPage />} />
      <Route path={paths.events} element={<EventsPage />} />
      <Route path={paths.eventsId} element={<EventsIdPage />} />
      <Route path={paths.settings} element={<SettingsPage />} />
      <Route path={paths.support} element={<SupportPage />} />
    </Routes>
  );
};

export default Router;
