import { useLocation } from "react-router-dom";

import Input from "components/Input";
import Button from "components/Button";
import File from "components/File";
import Typography from "components/Typography";

import "./styles.scss";

type CardProps = {
  title?: string;
  variant?: "users" | "partners" | "large" | "small";
  files?: { id: number; name: string }[];
  text?: string;
  setShow?: (show: boolean) => void;
};

const Card: React.FC<CardProps> = ({
  variant,
  title,
  files,
  text,
  setShow,
}) => {
  const { state } = useLocation();

  if (variant === "users") {
    return (
      <div className="card-header">
        <div className="card-header__wrapper">
          <div className="card-header__inner">
            <div className="card-header__item">
              <Typography>Косметолог</Typography>
              <Typography>{state?.user?.name}</Typography>
            </div>
            <div className="card-header__item">
              <Typography>Город</Typography>
              <Typography>{state?.user?.city}</Typography>
            </div>
            <div className="card-header__item">
              <Typography>Адрес доставки (выбрано)</Typography>
              <Typography>Будапештская улица, 46, Санкт-Петербург</Typography>
            </div>
          </div>
          <div className="card-header__inner">
            <div className="card-header__item">
              <Typography>ID</Typography>
              <Typography>{state?.user?.id}</Typography>
            </div>

            <div className="card-header__item">
              <Typography>Телефон</Typography>
              <Typography>{state?.user?.phone}</Typography>
            </div>

            <div className="card-header__item">
              <Typography>Адрес доставки</Typography>
              <Typography>ул. Коллонтай 10, кв. 211, Смоленск</Typography>
            </div>
          </div>
          <div className="card-header__inner">
            <div className="card-header__item__flex">
              <Input value="Ожидает верификации" />
              <Button
                size={"small"}
                onClick={() => {
                  setShow?.(true);
                }}
              >
                Редактировать
              </Button>
            </div>
            <div className="card-header__item">
              <Typography>Email</Typography>
              <Typography>userexample@mail.ru</Typography>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (variant === "partners") {
    return (
      <div className="card-header">
        <div className="card-header__wrapper">
          <div className="card-header__inner">
            <div className="card-header__item">
              <Typography>Производитель</Typography>
              <Typography style={{ fontSize: "17px", fontWeight: 600 }}>
                {state?.user?.name}
              </Typography>
            </div>
            <div className="card-header__item">
              <Typography>Город</Typography>
              <Typography>{state?.user?.city}</Typography>
            </div>
            <div className="card-header__item">
              <Typography>ФИО ответственного</Typography>
              <Typography>Меркушев Артемий Сергеевич</Typography>
            </div>
            <div className="card-header__item">
              <Typography>Менеджер</Typography>
              <Typography>Куликова Анастасия</Typography>
            </div>
          </div>
          <div className="card-header__inner">
            <div className="card-header__item">
              <Typography>ID</Typography>
              <Typography>{state?.user?.id}</Typography>
            </div>

            <div className="card-header__item">
              <Typography>Email</Typography>
              <Typography>userexample@mail.ru</Typography>
            </div>
          </div>
          <div className="card-header__inner">
            <div className="card-header__item__flex">
              <Input value="Подтвержден" />
              <Button size={"small"} onClick={() => setShow?.(true)}>
                Редактировать
              </Button>
            </div>
            <div className="card-header__item">
              <Typography>Телефон</Typography>
              <Typography>{state?.user?.phone}</Typography>
            </div>
          </div>
        </div>
        <div className="card-header__bottom">
          <Typography variant="subtitle2" style={{ padding: "12px 0" }}>
            Договор
          </Typography>

          <div className={"card-header__files"}>
            {files?.map((el) => (
              <div className={"card-header__file"}>
                <File fileName={el.name} key={el.id} />
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="card">
      {title && <Typography variant="subtitle1">{title}</Typography>}
      {files?.map((el) => (
        <div className={"card__file"}>
          <File fileName={el.name} key={el.id} />
        </div>
      ))}
      {text && <Typography className="card__text">{text}</Typography>}
    </div>
  );
};

export default Card;
