const PartnersPage = () => {
  const title = "Партнеры";
  const navigate = useNavigate();
  const [show, setShow] = useState(false);

  return (
    <Wrapper>
      <Title>{title}</Title>
      <Table
        headCells={headCells}
        rows={rows}
        title={title}
        headerButton={
          <Button
            onClick={() => {
              setShow(true);
            }}
            startIcon={<AddIcon />}
          >
            Создать
          </Button>
        }
        handleClick={(user) => {
          if (user) {
            const userState = { ...user };
            delete userState.status;
            navigate(`${paths.partners}/${user.id}`, {
              state: { user: userState },
            });
          }
        }}
      />
      {show && <PartnersPageEdit setShow={setShow} />}
    </Wrapper>
  );
};

export default PartnersPage;
