import * as React from "react";
import Box from "@mui/material/Box";
import TableMUI from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TablePagination from "@mui/material/TablePagination";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { Data, HeadCell, Order } from "./types";
import TableHead from "./TableHead";
import { styled, Typography } from "@mui/material";

import Search from "components/Search";
import Button from "components/Button";

import "./styles.scss";

import { ReactComponent as FilterIcon } from "assets/icons/filter-icon.svg";

function descendingComparator<T>(a: T, b: T, orderBy: keyof T) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator<Key extends keyof any>(
  order: Order,
  orderBy: Key
): (
  a: { [key in Key]: number | string },
  b: { [key in Key]: number | string }
) => number {
  return order === "desc"
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  cursor: "pointer",
  "&:nth-of-type(odd)": {
    backgroundColor: theme.palette.action.hover,
  },
  "td, th": {
    border: "1px solid var(--gray2)",
  },
}));

type Row = { [key: string]: string | number | React.ReactNode };

type TableProps = {
  headCells: HeadCell[];
  rows: Row[];
  handleClick?: (e?: Row) => void;
  title?: string;
  headerButton?: React.ReactNode;
  disablePagination?: boolean;
};

const Table: React.FC<TableProps> = ({
  headCells,
  rows,
  handleClick,
  title,
  headerButton,
  disablePagination,
}) => {
  const [order, setOrder] = React.useState<Order>("asc");
  const [orderBy, setOrderBy] = React.useState<keyof Data>("id");
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);
  const [search, setSearch] = React.useState("");

  const handleRequestSort = (
    _: React.MouseEvent<unknown>,
    property: keyof Data
  ) => {
    const isAsc = orderBy === property && order === "asc";
    setOrder(isAsc ? "desc" : "asc");
    setOrderBy(property);
  };

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const emptyRows =
    page > 0 ? Math.max(0, (1 + page) * rowsPerPage - rows.length) : 0;

  const visibleRows = React.useMemo(() => {
    if (typeof orderBy === "string") {
      return (
        [...rows]
          //@ts-ignore
          .sort(getComparator(order, orderBy))
          .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
      );
    }
    return rows;
  }, [order, orderBy, page, rows, rowsPerPage]);

  return (
    <div className="table">
      <div className="table__header">
        {title && <Typography className="table__title">{title}</Typography>}

        <Search
          value={search}
          placeholder="Поиск"
          style={{ maxWidth: 377, margin: "0 10px 0 0" }}
          onChange={(value) => {
            setSearch(value);
          }}
        />
        <Button startIcon={<FilterIcon />} variant="outlined">
          Фильтры
        </Button>
        <div className="table__count">
          <Typography
            variant="subtitle2"
            style={{ display: "flex", color: "#6C6C6C" }}
          >
            Всего:&nbsp;
            <Typography variant="subtitle2" style={{ color: "#101010" }}>
              899
            </Typography>
          </Typography>
        </div>
        {headerButton && <div style={{ marginLeft: 20 }}>{headerButton}</div>}
      </div>
      <Box sx={{ width: "100%" }}>
        <Paper sx={{ width: "100%" }} elevation={0}>
          <TableContainer>
            <TableMUI
              sx={{ minWidth: 750 }}
              aria-labelledby="tableTitle"
              size={"small"}
            >
              <TableHead
                order={order}
                orderBy={orderBy}
                onRequestSort={handleRequestSort}
                rowCount={rows.length}
                headCells={headCells}
                onSelectAllClick={() => {}}
                numSelected={0}
              />
              <TableBody>
                {visibleRows?.map((row) => {
                  return (
                    <StyledTableRow
                      key={typeof row === "string" ? row : Math.random()}
                    >
                      {Object.values(row).map((cell) => (
                        <TableCell
                          align={"left"}
                          onClick={() => {
                            handleClick?.(row);
                          }}
                        >
                          {cell}
                        </TableCell>
                      ))}
                    </StyledTableRow>
                  );
                })}
                {emptyRows > 0 && (
                  <TableRow
                    style={{
                      height: 53 * emptyRows,
                    }}
                  >
                    <TableCell colSpan={6} />
                  </TableRow>
                )}
              </TableBody>
            </TableMUI>
          </TableContainer>
          {!disablePagination && (
            <TablePagination
              rowsPerPageOptions={[5, 10, 25]}
              component="div"
              count={rows.length}
              rowsPerPage={rowsPerPage}
              page={page}
              onPageChange={handleChangePage}
              onRowsPerPageChange={handleChangeRowsPerPage}
              labelRowsPerPage={"Строк на странице"}
            />
          )}
        </Paper>
      </Box>
    </div>
  );
};

export default Table;
