import Typography from "components/Typography";

import "./styles.scss";

type OrdersProps = {
  data: {
    id: number;
    name: string;
    count: number;
    image: string;
  }[];
};

const Orders: React.FC<OrdersProps> = ({ data }) => {
  return (
    <div className="orders">
      <div className="orders__header"></div>
      <div className="orders__list">
        {data?.map((el: any) => (
          <div className="orders__item" key={el.id}>
            <img src={el.image} alt="Product" className="orders__image" />
            <Typography style={{ marginRight: 20, width: 246 }}>
              {el.name}
            </Typography>
            <Typography style={{ marginRight: 20, width: 90 }}>
              {el.id}
            </Typography>
            <Typography style={{ marginRight: 20, width: 60 }}>
              {el.count} шт
            </Typography>
            <Typography>{el.price}</Typography>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Orders;
