const EventsPage = () => {
  const navigate = useNavigate();
  const title = "События";
  const [show, setShow] = useState(false);

  return (
    <Wrapper>
      <Title>{title}</Title>
      <Table
        headCells={headCells}
        rows={rows}
        title={title}
        headerButton={
          <Button
            onClick={() => {
              setShow(true);
            }}
            startIcon={<AddIcon />}
          >
            Создать
          </Button>
        }
        handleClick={(user) => {
          if (user) {
            const userState = { ...user };
            delete userState.status;
            navigate(`${paths.events}/${user.id}`, {
              state: { user: userState },
            });
          }
        }}
      />
      {show && <EventsPageEdit setShow={setShow} />}
    </Wrapper>
  );
};

export default EventsPage;
